const QQMapWX = require('../../libs/qqmap-wx-jssdk.min.js');
export default {
	data() {
		return {
			address: "正在获取地址...",
			longitude: 114.010857,
			latitude: 22.63137,
			current_long: 114.010857,
			current_lat: 22.63137,
			key: 'W4WBZ-TUD65-IDAIR-QPM36-HMFQ5-CGBZP',
			mapCtx: null,
			location: false,
			qqmapsdk: null,
			mapObj: null
		}
	},
	onLoad() {
		this.qqmapsdk = new QQMapWX({
			key: this.key
		});
		this.currentLocation()
	},
	onReady() {




















	},
	methods: {
		regionchange(e) {
			// 地图发生变化的时候，获取中间点，也就是cover-image指定的位置
			if (e.type == 'end' && (e.causedBy == 'scale' || e.causedBy == 'drag')) {
				this.address = "正在获取地址...";
				if (!this.mapCtx) {
					this.mapCtx = uni.createMapContext("maps");
				}
				this.mapCtx.getCenterLocation({
					type: 'gcj02',
					success: (res) => {
						//console.log(res)
						this.latitude = res.latitude;
						this.longitude = res.longitude;
						this.getAddress(res.longitude, res.latitude);
					}
				})
			}
		},
		getAddress: function(lng, lat) {
			//根据经纬度获取地址信息
			this.qqmapsdk.reverseGeocoder({
				location: {
					latitude: lat,
					longitude: lng
				},
				success: (res) => {
					//console.log(res)
					this.address = res.result.formatted_addresses.recommend
				},
				fail: (res) => {
					this.address = "获取位置信息失败"
				}
			})
		},
		currentLocation() {
			//当前位置
			const that = this;
			uni.getLocation({
				type: 'gcj02',
				success(res) {
					that.latitude = res.latitude;
					that.longitude = res.longitude;
					//that.getAddress(res.longitude, res.latitude);
				}
			})
		}
	}
}
